/* eslint-disable no-param-reassign */
import { createSlice } from '@reduxjs/toolkit';

export const expandState = createSlice({
  name: 'expandController',
  initialState: {
    value: false,
  },
  reducers: {
    setExpandFalse: (state) => {
      state.value = false;
    },
    setExpandTrue: (state) => {
      state.value = true;
    },
    setExpandByState: (state, action) => {
      state.value = action.payload;
    },
  },
});

export const {
  setExpandFalse,
  setExpandTrue,
  setExpandByState,
} = expandState.actions;

export default expandState.reducer;
