import React from 'react';

import SpecBlock from './SpecBlock';

export default function PopupHeader({
  list,
}) {
  return (
    <div className="info-section info-header-section">
      <div className="info-spec">
        <h2 className="info-title copy-heading">
          {list.name}
        </h2>
        <div className="info-spec-wrap">
          <div className="spec-block-wrap">
            {list.spec?.map((item) => (
              <SpecBlock
                title={item.title}
                caption={item.caption}
                content={item.content}
              />
            ))}
          </div>
        </div>
      </div>
      <div className="info-illust">
        <img className="info-illust" src={process.env.PUBLIC_URL + list.illust} alt={list.name} />
      </div>
    </div>
  );
}
