import React, { forwardRef } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { setCabinMapFalse, setCabinMapUndefined } from '../../Store/cabinMapState';
import { setFinVideoTrue } from '../../Store/finVideoState';
import { setFinStoryTrue } from '../../Store/finStoryState';
import { setHillsStoryTrue } from '../../Store/hillsStoryState';
import { setDetailPopupTrue } from '../../Store/detailPopupState';
import {
  setMapFin, setMapHills, setMapHome, setMapPlanB,
} from '../../Store/mapState';

function Navigation({
  addTransition,
  setPinChanger,
  setImageChange,
  finfocusSpot,
  hillsfocusSpot,
  finPinsHandler,
  sFinData,
  hillsPinsHandler,
  hillsPinsState,
  sHillsData,
}) {
  const dispatch = useDispatch();

  const mapController = useSelector((state) => state.mapController.value);
  const cabinMapController = useSelector((state) => state.cabinMapController.value);
  const detailPopupController = useSelector((state) => state.detailPopupController.value);

  const pinValue = JSON.parse(localStorage.getItem('pinValue'));

  return (
    <div
      className={`
        map-swiper
        ${detailPopupController ? 'hide-swiper' : ''}
        ${cabinMapController ? 'hide-swiper' : ''}`}
    >
      <ul>
        <li
          className={`swiper ${mapController === 'home' ? 'active' : ''}`}
          onClick={() => {
            dispatch(setMapHome());
            addTransition();
            dispatch(setCabinMapFalse());
          }}
          role="presentation"
        >
          <i className={
            `marker ${mapController === 'home' ? 'active' : ''}`
            }
          />
          ALT A
        </li>
        <li
          className={
            `swiper ${mapController === 'tab1' ? 'active' : ''}`
          }
          onClick={() => {
            dispatch(setMapFin());
            finPinsHandler();
            hillsPinsHandler();
          }}
          onMouseEnter={() => { setPinChanger('fin-hover'); }}
          onMouseLeave={() => { setPinChanger(); }}
          role="presentation"
        >
          <i className={`marker ${mapController === 'tab1' ? 'active' : ''}`} />
          SONO FIN VIVALDI
          <ul className={`area-list ${mapController === 'tab1' ? 'area-list-max' : ''}`}>
            <li
              onClick={() => { dispatch(setFinVideoTrue()); }}
              role="presentation"
            >
              Fin. Teaser Video
            </li>
            <li
              onClick={() => { dispatch(setFinStoryTrue()); }}
              role="presentation"
            >
              Fin. Brand Story
            </li>
            {sFinData.dataList.map((list, index) => (
              <li
                className={pinValue === index ? 'list-selected' : ''}
                onClick={() => {
                  addTransition();
                  finfocusSpot(index);
                  finPinsHandler(index);
                  setImageChange(0);
                  dispatch(setCabinMapUndefined());
                  dispatch(setDetailPopupTrue());
                  localStorage.setItem('pinValue', index);
                }}
                role="presentation"
              >
                {`${list.number}. ${list.name}`}
              </li>
            ))}
          </ul>
        </li>
        <li
          className={
            `swiper ${mapController === 'tab2' ? 'active' : ''}`
          }
          onClick={() => {
            dispatch(setMapHills());
            finPinsHandler();
            hillsPinsHandler();
            dispatch(setCabinMapFalse());
          }}
          onMouseEnter={() => { setPinChanger('hills-hover'); }}
          onMouseLeave={() => { setPinChanger(); }}
          role="presentation"
        >
          <i className={`marker ${mapController === 'tab2' ? 'active' : ''}`} />
          SONO HILLS
          <ul className={`area-list ${mapController === 'tab2' ? 'area-list-max' : ''}`}>
            <li
              onClick={() => {
                dispatch(setHillsStoryTrue());
              }}
              role="presentation"
            >
              0. Brand Story
            </li>
            {sHillsData.dataList.map((list, index) => (
              <li
                className={hillsPinsState === index ? 'list-selected' : ''}
                onClick={() => {
                  addTransition();
                  setImageChange(0);
                  hillsfocusSpot(index);
                  hillsPinsHandler(index);
                }}
                role="presentation"
                key={list.name}
              >
                {`${index + 1}. ${list.name}`}
              </li>
            ))}
          </ul>
        </li>
        <li
          className={`swiper ${mapController === 'planB' ? 'active' : ''}`}
          onClick={() => { dispatch(setMapPlanB()); }}
          role="presentation"
        >
          <i className={
            `marker ${mapController === 'planB' ? 'active' : ''}`
            }
          />
          ALT B
        </li>
      </ul>
    </div>
  );
}

export default forwardRef(Navigation);
