import React from 'react';
import { useSelector } from 'react-redux';
import generalImg from '../../db/generalImg.json';

export default function MapPopup() {
  const mapController = useSelector((state) => state.mapController.value);
  return (
    <div className={`map-popup-wrap ${mapController === 'planB' ? 'show-map-popup' : ''}`}>
      <img src={process.env.PUBLIC_URL + generalImg.images[0].masterplan_b} alt="masterplan b" />
    </div>
  );
}
