import React, { useEffect, useRef, forwardRef } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { setFinVideoFalse } from '../../Store/finVideoState';

import finBrandStoryData from '../../db/finBrandStory.json';
import generalImage from '../../db/generalImg.json';

import CloseBtn from '../CloseBtn';

function FinVideo() {
  const dispatch = useDispatch();
  const finVideoController = useSelector((state) => state.finVideoController.value);
  const videoRef = useRef();

  useEffect(() => {
    if (finVideoController) {
      videoRef.current.play();
    } else {
      videoRef.current.pause();
    }
  });

  return (
    <div className={`video-container ${finVideoController === true ? 'show-video-container' : ''}`}>
      <CloseBtn
        generalImage={generalImage}
        onClickHandler={() => {
          dispatch(setFinVideoFalse());
          // eslint-disable-next-line react/destructuring-assignment
        }}
      />
      <video
        className="fin-brand-story-video"
        controls
        src={process.env.PUBLIC_URL + finBrandStoryData.finBrandStoryVideo}
        ref={videoRef}
      >
        <track kind="captions" />
      </video>
    </div>
  );
}

export default forwardRef(FinVideo);
