import React, {
  forwardRef,
  useState,
  useRef,
} from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { setFinStoryFalse } from '../../Store/finStoryState';
import generalImage from '../../db/generalImg.json';
import finBrandStoryData from '../../db/finBrandStory.json';

import CloseBtn from '../CloseBtn';

function FinBrandStory() {
  const dispatch = useDispatch();
  const finStoryController = useSelector((state) => state.finStoryController.value);
  const videoRef = useRef([]);

  const [brandStoryImage, setTeaserImage] = useState(0);
  const brandStoryImageHandler = (index) => { setTeaserImage(index); };

  return (
    <div className={`
      fin-brand-story-container
      ${finStoryController ? 'show-brand-story-container' : ''}`}
    >
      <div className="brand-story-nav-container">
        <CloseBtn
          generalImage={generalImage}
          onClickHandler={() => {
            dispatch(setFinStoryFalse());
            brandStoryImageHandler(0);
          }}
        />
        <ul className="brand-story-numbering">
          <div
            className="left-btn btn"
            role="presentation"
            onClick={() => {
              if (brandStoryImage === 0) {
                setTeaserImage(0);
              } else {
                setTeaserImage(brandStoryImage - 1);
              }
            }}
          >
            <img src={process.env.PUBLIC_URL + generalImage.icons[0].chevronLeft} alt="left button" />
          </div>
          {finBrandStoryData.finBrandStory.map((list, i) => (
            <li
              className={`copy-body-small ${brandStoryImage === i ? 'select-num num' : 'num'}`}
              onClick={() => {
                brandStoryImageHandler(i);
              }}
              role="presentation"
              key={list.section}
            >
              <i className={brandStoryImage === i ? 'show-marker marker' : 'marker'} />
              {list.section}
            </li>
          ))}
          <div
            className="right-btn btn"
            role="presentation"
            onClick={() => {
              if (brandStoryImage === finBrandStoryData.finBrandStory.length - 1) {
                setTeaserImage(finBrandStoryData.finBrandStory.length - 1);
              } else {
                setTeaserImage(brandStoryImage + 1);
              }
            }}
          >
            <img src={process.env.PUBLIC_URL + generalImage.icons[0].chevronRight} alt="right button" />
          </div>
        </ul>
      </div>

      <div className="brand-story-content-wrap">
        {finBrandStoryData.finBrandStory.map((list, i) => (
          <div
            className={brandStoryImage === i ? 'show-brand-story-content brand-story-content' : 'brand-story-content'}
            key={list.section}
          >
            <div className="brand-story-text-wrap">
              <div className="content-caption">
                <p className="caption copy-body-large">
                  {list.caption}
                </p>
                <p className="caption-heading copy-body-large">
                  {list.caption_heading}
                </p>
                <p className="caption-body copy-body-large">
                  {list.caption_body}
                </p>
              </div>

              <h1 className="content-heading copy-heading">
                {list.title}
              </h1>
              {
                list.sub_heading === undefined
                  ? null
                  : (
                    <h2 className="content-sub-heading copy-sub-heading">
                      {list.sub_heading}
                    </h2>
                  )
              }
              {
                list.body === undefined
                  ? null
                  : (
                    <p className="content-body copy-body">
                      {list.body}
                    </p>
                  )
              }
              {
                  list.image === undefined
                    ? null
                    : (
                      <div className="content-image">
                        <img src={process.env.PUBLIC_URL + list.image} alt={list.caption} />
                        <p className="copy-body-small">
                          {list.comment}
                        </p>
                      </div>
                    )
                }
            </div>
            <div className="image-video-wrap">
              {
                list.bg_image === undefined
                  ? null
                  : (
                    <img
                      className="brand-story-image"
                      src={process.env.PUBLIC_URL + list.bg_image}
                      alt={list.title}
                      key={list.section}
                    />
                  )
              }
              {
                list.video === undefined
                  ? null
                  : (
                    <video
                      className="fin-brand-story-video"
                      controls
                      src={process.env.PUBLIC_URL + list.video}
                      ref={(el) => { videoRef.current.push(el); }}
                    >
                      <track kind="captions" />
                    </video>
                  )
              }
            </div>
          </div>
        ))}
      </div>
    </div>
  );
}

export default forwardRef(FinBrandStory);
