/* eslint-disable no-param-reassign */
import { createSlice } from '@reduxjs/toolkit';

export const cabinMapState = createSlice({
  name: 'cabinMapController',
  initialState: {
    value: false,
  },
  reducers: {
    setCabinMapFalse: (state) => {
      state.value = false;
    },
    setCabinMapTrue: (state) => {
      state.value = true;
    },
    setCabinMapUndefined: (state) => {
      state.value = undefined;
    },
    setCabinMapByState: (state, action) => {
      state.value = action.payload;
    },
  },
});

export const {
  setCabinMapTrue,
  setCabinMapFalse,
  setCabinMapUndefined,
  setCabinMapByState,
} = cabinMapState.actions;

export default cabinMapState.reducer;
