import React from 'react';

export default function Tag({
  i,
  imageChange,
  imageChangeHandler,
  setSlide,
  tag,
}) {
  return (
    <li
      className={imageChange === i ? 'selected' : 'none'}
      onClick={() => {
        imageChangeHandler(i);
        setSlide(0);
      }}
      role="presentation"
    >
      {tag}
    </li>
  );
}
