import React from 'react';

export default function Career({
  i,
  imageChange,
  careerTitle,
  listItem,
}) {
  return (
    <div className={imageChange === i ? 'info-career' : 'info-career hide-career'}>
      <p className="copy-body">
        {careerTitle}
      </p>
      {listItem?.map((li) => (
        <a
          href={li.link}
          target="_blank"
          rel="noreferrer"
          className="copy-body-small career"
        >
          {li.title}
        </a>
      ))}
    </div>
  );
}
