/* eslint-disable no-param-reassign */
import { createSlice } from '@reduxjs/toolkit';

export const finStoryState = createSlice({
  name: 'finStoryController',
  initialState: {
    value: false,
  },
  reducers: {
    setFinStoryFalse: (state) => {
      state.value = false;
    },
    setFinStoryTrue: (state) => {
      state.value = true;
    },
    setFinStoryByState: (state, action) => {
      state.value = action.payload;
    },
  },
});

export const {
  setFinStoryFalse,
  setFinStoryTrue,
  setFinStoryByState,
} = finStoryState.actions;

export default finStoryState.reducer;
