import { configureStore } from '@reduxjs/toolkit';

import finVideoReducer from './finVideoState';
import finStoryReducer from './finStoryState';
import hillsStoryReducer from './hillsStoryState';
import mapReducer from './mapState';
import cabinMapReducer from './cabinMapState';
import expandReducer from './expandState';
import detailPopupReducer from './detailPopupState';

export default configureStore({
  reducer: {
    finVideoController: finVideoReducer,
    finStoryController: finStoryReducer,
    hillsStoryController: hillsStoryReducer,
    mapController: mapReducer,
    detailPopupController: detailPopupReducer,
    cabinMapController: cabinMapReducer,
    expandController: expandReducer,
  },
});
