/* eslint-disable use-isnan */
import React, { forwardRef } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { setDetailPopupTrue } from '../../Store/detailPopupState';

function Pins({
  data,
  pinsHandler,
  addTransition,
  setImageChange,
  focusSpot,
  mainTab,
  classList,
  sFinFacData,
  // hillsFacData,
}, ref) {
  const dispatch = useDispatch();
  const mapController = useSelector((state) => state.mapController.value);
  const detailPopupController = useSelector((state) => state.detailPopupController.value);
  const pinValue = JSON.parse(localStorage.getItem('pinValue'));

  const pins = data.dataList.map((list, index) => (
    <div
      className={pinValue === index ? `${classList}-selected ${classList} pin-box` : `${classList} pin-box`}
      ref={ref}
      onClick={() => {
        if (data !== sFinFacData) {
          localStorage.setItem('pinValue', index);
          dispatch(setDetailPopupTrue());
          addTransition();
          setImageChange(0);
          focusSpot(index);
          pinsHandler(index);
        }
      }}
      key={list.name}
      role="presentation"
    >
      <div className={detailPopupController ? 'hide-pin pin' : 'pin'}>
        <p className={`pin-head ${mapController === mainTab ? 'show-pin-head' : ''}`} />
        <div className={`pin-line ${mapController === mainTab ? 'show-pin-line' : ''}`} />
        <div className={`pin-icon ${mapController === mainTab ? 'show-pin-icon' : ''}`}>
          <img src={process.env.PUBLIC_URL + list.icon} alt={`${list.name} icon`} />
        </div>
        { list.number === undefined
          ? (
            <div className={`pin-title ${mapController === mainTab ? 'show-pin-title' : ''}`}>
              {list.name}
            </div>
          )
          : (
            <div className={`pin-title ${mapController === mainTab ? 'show-pin-title' : ''}`}>
              {`${list.number}. ${list.name}`}
            </div>
          )}
      </div>
      <img
        className="area-mark"
        src={process.env.PUBLIC_URL + list.mark}
        alt={list.name}
      />
    </div>
  ));

  return (
    <div className={`${classList}-wrap pin-wrap`}>
      {pins}
    </div>
  );
}

export default forwardRef(Pins);
