/* eslint-disable no-nested-ternary */
import { forwardRef, React } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { setDetailPopupFalse } from '../../Store/detailPopupState';
import { setCabinMapFalse } from '../../Store/cabinMapState';
import { setExpandFalse, setExpandTrue } from '../../Store/expandState';

import PopupHeader from './PopupHeader/PopupHeader';
import PopupFacility from './PopupFacility/PopupFacility';
// import PopupAreaContext from './PopupAreaContext/PopupAreaContext';
import PopupUser from './PopupUser/PopupUser';

import finData from '../../db/sFinData.json';
import cabinData from '../../db/modular.json';

function DetailInfo({
  addTransition,
  // setImageChange,
  imageChange,
  imageChangeHandler,
  mapTab,
  setPinChanger,
  slide,
  setSlide,
  slideDotChanger,
  setCabinFocus,
}, ref) {
  const dispatch = useDispatch();
  const mapController = useSelector((state) => state.mapController.value);
  const expandController = useSelector((state) => state.expandController.value);
  const detailPopupController = useSelector((state) => state.detailPopupController.value);

  const pinValue = JSON.parse(localStorage.getItem('pinValue'));
  const cabinValue = JSON.parse(localStorage.getItem('cabinValue'));

  return (
    <div className={mapController === mapTab ? 'show-area-info area-info' : 'area-info'}>
      <div
        className={detailPopupController ? 'info info-showing' : 'info'}
        ref={ref}
      >
        <div
          className="close"
          onClick={(e) => {
            e.target.parentNode.classList.remove('info-expanding');
            setTimeout(1000, localStorage.removeItem('pinValue'));
            localStorage.removeItem('cabinValue');
            dispatch(setExpandFalse());
            dispatch(setCabinMapFalse());
            dispatch(setDetailPopupFalse());
            addTransition();
            setSlide(0);
            setTimeout(1000, setSlide(0));
            setPinChanger();
            setCabinFocus(undefined);
            // setTimeout(1000, setImageChange(0));
            // imageChange(0);
          }}
          role="presentation"
        >
          <span className="bar" />
          <span className="bar" />
        </div>

        <div
          className={expandController === false ? 'expander reduction' : 'expander'}
          onClick={(e) => {
            if (!e.target.parentNode.classList.contains('info-expanding')) {
              e.target.parentNode.classList.add('info-expanding');
              dispatch(setExpandTrue());
            } else {
              e.target.parentNode.classList.remove('info-expanding');
              dispatch(setExpandFalse());
            }
          }}
          role="presentation"
        >
          <span className="bar" />
          <span className="bar" />
          <span className="bar" />
          <span className="bar" />
          <span className="bar" />
          <span className="bar" />
          <span className="bar" />
          <span className="bar" />
          <span className="bar" />
        </div>

        <div className="info-content">
          {pinValue
            ? <PopupHeader list={finData.dataList[pinValue]} />
            : cabinValue
              ? <PopupHeader list={cabinData.dataList[cabinValue - 1]} />
              : null}
          <div className="info-section info-body-section">
            {pinValue
              ? (
                <PopupFacility
                  list={finData.dataList[pinValue]}
                  imageChangeHandler={imageChangeHandler}
                  setSlide={setSlide}
                  slide={slide}
                  imageChange={imageChange}
                  slideDotChanger={slideDotChanger}
                />
              ) : cabinValue
                ? (
                  <PopupFacility
                    list={cabinData.dataList[cabinValue - 1]}
                    imageChangeHandler={imageChangeHandler}
                    setSlide={setSlide}
                    slide={slide}
                    imageChange={imageChange}
                    slideDotChanger={slideDotChanger}
                  />
                )
                : null }
            {/*
            finData.dataList[pinValue]
              ? <PopupAreaContext list={finData.dataList[pinValue]} />
              : null
              */}
            {finData.dataList[pinValue]
              ? <PopupUser list={finData.dataList[pinValue]} />
              : null}
          </div>
        </div>
      </div>
    </div>
  );
}

export default forwardRef(DetailInfo);
