/* eslint-disable no-param-reassign */
import { createSlice } from '@reduxjs/toolkit';

export const mapState = createSlice({
  name: 'mapController',
  initialState: {
    value: 'home',
  },
  reducers: {
    setMapHome: (state) => {
      state.value = 'home';
    },
    setMapFin: (state) => {
      state.value = 'tab1';
    },
    setMapHills: (state) => {
      state.value = 'tab2';
    },
    setMapPlanB: (state) => {
      state.value = 'planB';
    },
    setMapByState: (state, action) => {
      state.value = action.payload;
    },
  },
});

export const {
  setMapHome,
  setMapFin,
  setMapHills,
  setMapPlanB,
  setMapByState,
} = mapState.actions;

export default mapState.reducer;
