import React from 'react';

export default function Description({
  i,
  imageChange,
  desc,
}) {
  return (
    <div className={imageChange === i && desc !== undefined ? 'info-desc' : 'info-desc hide-desc'}>
      <p>
        <span className="copy-body-small">
          {desc}
        </span>
      </p>
    </div>
  );
}
