import React, { forwardRef } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { setCabinMapTrue } from '../../Store/cabinMapState';
import { setDetailPopupTrue } from '../../Store/detailPopupState';
import modular from '../../db/modular.json';

function CabinChart({
  cabinFocus,
  cabinFocusHandler,
  // setFinPinsState,
  cabinHandler,
  setPinChanger,
  setImageChange,
  setPos,
}) {
  const dispatch = useDispatch();

  const mapController = useSelector((state) => state.mapController.value);
  // const detailPopupController = useSelector((state) => state.detailPopupController.value);

  return (
    <div className={
      `modular-info-chart ${mapController !== 'tab1' ? 'hide-modular-info-chart' : ''}`
      }
    >
      <div className="modular-info-table">
        {modular.info.map((item, index) => (
          <div className="modular-info-row" key={item.title}>
            <div
              className={`
                info
                ${item.title !== undefined ? 'info-title' : ''}
                ${cabinFocus === index ? 'focus-info' : ''}
                copy-body-small
              `}
              onClick={() => {
                dispatch(setCabinMapTrue());
                dispatch(setDetailPopupTrue());
                cabinFocusHandler(index);
                // setFinPinsState(false);
                cabinHandler(index);
                setPinChanger('fin-cabin-mode');
                setImageChange(0);
                setPos({ x: 0, y: 0, scale: 1 });
                localStorage.setItem('cabinValue', index);
              }}
              role="presentation"
            >
              <p>
                {item.title}
              </p>
            </div>
            <div className={`
              info
              info-shape
              ${cabinFocus !== undefined ? 'hide-info' : ''}
              `}
            >
              {item.shape === undefined
                ? ''
                : <img src={process.env.PUBLIC_URL + item.shape} alt={item.title} />}
            </div>
            <div className={`
              info
              info-keys
              copy-body-small
              ${cabinFocus !== undefined ? 'hide-info' : ''}
              `}
            >
              <p>
                {item.keys}
              </p>
            </div>
            <div className={`
              info
              info-percent
              copy-body-small
              ${cabinFocus !== undefined ? 'hide-info' : ''}
              `}
            >
              <p>
                {item.percent}
              </p>
            </div>
            <div className={`
              info
              info-g-a-key
              copy-body-small
              ${cabinFocus !== undefined ? 'hide-info' : ''}
              `}
            >
              <p>
                {item.g_a_key === undefined ? '' : item.g_a_key}
              </p>
            </div>
            <div className={`
              info
              info-total-g-a
              copy-body-small
              ${cabinFocus !== undefined ? 'hide-info' : ''}
              `}
            >
              <p>
                {item.total_g_a}
              </p>
            </div>
          </div>
        ))}
      </div>
    </div>
  );
}

export default forwardRef(CabinChart);
