import {
  React,
  forwardRef,
  useState,
} from 'react';

import { Link } from 'react-router-dom';

// import { Link } from 'react-router-dom';

import generalImg from '../db/generalImg.json';

function Intro() {
  const [error, setError] = useState('');
  const [password, setPassword] = useState();
  const handleSubmit = () => {
    if (password === '0701') {
      localStorage.setItem('status', 'loggedIn');
    } else {
      setError('wrong');
    }
  };

  return (
    <div className="intro">
      <div
        className="br-logo"
        role="presentation"
      >
        <img src={process.env.PUBLIC_URL + generalImg.icons[0].brLogoWhite} alt="br-logo" />
      </div>
      <p className="br-title copy-body-large">
        B|R GROUP
      </p>
      <form className="br-admin">
        <input
          type="password"
          onChange={(e) => {
            setPassword(e.target.value);
          }}
        />
        <button
          className="login"
          type="submit"
          onClick={handleSubmit}
        >
          <Link to="/">
            Login
          </Link>
        </button>
        <p className={`warning ${error}`}>
          Wrong password
        </p>
      </form>
    </div>
  );
}

export default forwardRef(Intro);
