/* eslint-disable no-param-reassign */
import { createSlice } from '@reduxjs/toolkit';

export const finVideoState = createSlice({
  name: 'finVideoController',
  initialState: {
    value: false,
  },
  reducers: {
    setFinVideoFalse: (state) => {
      state.value = false;
    },
    setFinVideoTrue: (state) => {
      state.value = true;
    },
    setFinVideoByState: (state, action) => {
      state.value = action.payload;
    },
  },
});

export const {
  setFinVideoFalse,
  setFinVideoTrue,
  setFinVideoByState,
} = finVideoState.actions;

export default finVideoState.reducer;
