import React, {
  forwardRef, useState, useEffect, useRef,
} from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { setHillsStoryFalse } from '../../Store/hillsStoryState';
import generalImage from '../../db/generalImg.json';
import hillsBrandStoryData from '../../db/hillsBrandStory.json';

import CloseBtn from '../CloseBtn';

function HillsBrandStory() {
  const dispatch = useDispatch();
  const hillsStoryController = useSelector((state) => state.hillsStoryController.value);

  const videoRef = useRef([]);
  const [textColor, setTextColor] = useState('dark');
  const [brandStoryImage, setTeaserImage] = useState(0);
  const brandStoryImageHandler = (index) => { setTeaserImage(index); };

  useEffect(() => {
    if (brandStoryImage === 4
      || brandStoryImage === 5
      || brandStoryImage === 6
      || brandStoryImage === 10
      || brandStoryImage === 11
      || brandStoryImage === 16
      || brandStoryImage === 17) {
      setTextColor('light');
    } else {
      setTextColor('dark');
    }
  }, [brandStoryImage]);

  return (
    <div className={`
      hills-brand-story-container
      ${hillsStoryController ? 'show-brand-story-container' : ''}`}
    >
      <div className="brand-story-nav-container">
        <CloseBtn
          generalImage={generalImage}
          onClickHandler={() => {
            dispatch(setHillsStoryFalse());
            brandStoryImageHandler(0);
            videoRef.current[0].pause();
            videoRef.current[1].pause();
          }}
        />
        <ul className="brand-story-numbering">
          <div
            className="left-btn btn"
            role="presentation"
            onClick={() => {
              if (brandStoryImage === 0) {
                setTeaserImage(0);
              } else {
                setTeaserImage(brandStoryImage - 1);
              }
              videoRef.current[0].pause();
              videoRef.current[1].pause();
            }}
          >
            <img src={process.env.PUBLIC_URL + generalImage.icons[0].chevronLeft} alt="left button" />
          </div>
          {hillsBrandStoryData.hillsBrandStory.map((list, i) => (
            <li
              className={`copy-body-small ${brandStoryImage === i ? 'select-num num' : 'num'}`}
              onClick={() => {
                brandStoryImageHandler(i);
                videoRef.current[0].pause();
                videoRef.current[1].pause();
              }}
              role="presentation"
              key={list.section}
            >
              <i className={brandStoryImage === i ? 'show-marker marker' : 'marker'} />
              {list.section}
            </li>
          ))}
          <div
            className="right-btn btn"
            role="presentation"
            onClick={() => {
              if (brandStoryImage === hillsBrandStoryData.hillsBrandStory.length - 1) {
                setTeaserImage(hillsBrandStoryData.hillsBrandStory.length - 1);
              } else {
                setTeaserImage(brandStoryImage + 1);
                videoRef.current[0].pause();
                videoRef.current[1].pause();
              }
            }}
          >
            <img src={process.env.PUBLIC_URL + generalImage.icons[0].chevronRight} alt="right button" />
          </div>
        </ul>
      </div>

      <div className="brand-story-content-wrap">
        {hillsBrandStoryData.hillsBrandStory.map((list, i) => (
          <div
            className={brandStoryImage === i ? 'show-brand-story-content brand-story-content' : 'brand-story-content'}
            key={list.section}
          >
            <div className="brand-story-text-wrap">
              <div className="content-caption">
                <p className={`caption copy-body-large ${textColor}`}>
                  {list.caption}
                </p>
                <p className={`caption-heading copy-body-large ${textColor}`}>
                  {list.caption_heading}
                </p>
                <p className={`caption-body copy-body-large ${textColor}`}>
                  {list.caption_body}
                </p>
              </div>

              <h1 className={`content-heading copy-heading ${textColor}`}>
                {list.title}
              </h1>
              <h2 className={`content-sub-heading copy-sub-heading ${textColor}`}>
                {list.sub_heading}
              </h2>
              <p className={`content-body copy-body ${textColor}`}>
                {list.body}
              </p>
              {
                  list.image === undefined
                    ? null
                    : (
                      <div className="content-image">
                        <img src={process.env.PUBLIC_URL + list.image} alt={list.caption} />
                        <p className="copy-body-small">
                          {list.comment}
                        </p>
                      </div>
                    )
                }
            </div>
            <div className="image-video-wrap">
              {
                list.bg_image === undefined
                  ? null
                  : (
                    <img
                      className="brand-story-image"
                      src={process.env.PUBLIC_URL + list.bg_image}
                      alt={list.title}
                      key={list.section}
                    />
                  )
              }
              {
                list.video === undefined
                  ? null
                  : (
                    <video
                      className="hills-brand-story-video"
                      controls
                      src={process.env.PUBLIC_URL + list.video}
                      ref={(el) => { videoRef.current.push(el); }}
                    >
                      <track kind="captions" />
                    </video>
                  )
              }
            </div>
          </div>
        ))}
      </div>
    </div>
  );
}

export default forwardRef(HillsBrandStory);
