import React, { forwardRef } from 'react';

function CloseBtn({
  onClickHandler,
  generalImage,
}) {
  return (
    <div
      className="close-btn"
      role="presentation"
      onClick={onClickHandler}
    >
      <img src={process.env.PUBLIC_URL + generalImage.icons[0].close_btn} alt="close button" />
    </div>
  );
}

export default forwardRef(CloseBtn);
