/* eslint-disable no-param-reassign */
import { createSlice } from '@reduxjs/toolkit';

export const hillsStoryState = createSlice({
  name: 'hillsStoryController',
  initialState: {
    value: false,
  },
  reducers: {
    setHillsStoryFalse: (state) => {
      state.value = false;
    },
    setHillsStoryTrue: (state) => {
      state.value = true;
    },
    setHillsStoryByState: (state, action) => {
      state.value = action.payload;
    },
  },
});

export const {
  setHillsStoryFalse,
  setHillsStoryTrue,
  setHillsStoryByState,
} = hillsStoryState.actions;

export default hillsStoryState.reducer;
