import React, { forwardRef } from 'react';
import { useSelector } from 'react-redux';
import generalImg from '../../db/generalImg.json';
import sFinData from '../../db/sFinData.json';
import sFinFacData from '../../db/sFinFacData.json';
import sHillsFacData from '../../db/sHillsFacData.json';
// import sHillsData from '../../db/sHillsData.json';

import Pins from './Pins';
import Zoning from './Zoning';

function PinContainer({
  posX,
  posY,
  posScale,
  pinChanger,
  cabinFocus,
  addTransition,
  setImageChange,
  finfocusSpot,
  finPinsHandler,
  finPinsState,
  // hillsPinsState,
  // hillsfocusSpot,
  // hillsPinsHandler,
}, ref) {
  const {
    containerRef,
    pinContainerRef,
    finfacRef,
    // hillsfacRef,
  } = ref;

  const mapController = useSelector((state) => state.mapController.value);
  const cabinMapController = useSelector((state) => state.cabinMapController.value);
  const detailPopupController = useSelector((state) => state.detailPopupController.value);

  return (
    <div
      className="map-container"
      style={
        detailPopupController
          ? {
            transformOrigin: '0 0',
            transform: `translate(${posX}px, ${posY}px) scale(${posScale})`,
          }
          : {
            transformOrigin: '0 0',
            transform: 'translate(0px, 0px) scale(1)',
          }
      }
      ref={containerRef}
    >
      <div
        className={`
        ${mapController === 'home' ? 'masterplan' : ''}
        ${mapController === 'tab1' ? 'masterplan masterplan-fin' : ''}
        ${mapController === 'tab2' ? 'masterplan masterplan-hills' : ''}
        `}
      >
        <div
          className={`masterplan-fin-container
            ${pinChanger === 'fin-hover' ? 'show-fin-container' : ''}
            ${pinChanger === 'fin-cabin-mode' ? 'move-left-fin-container' : ''}
          `}
          ref={mapController === 'tab1' ? pinContainerRef : null}
        >
          <Pins
            classList="fin-pin"
            mainTab="tab1"
            data={sFinData}
            sFinFacData={sFinFacData}
            hillsFacData={sHillsFacData}
            pinState={finPinsState}
            addTransition={addTransition}
            setImageChange={setImageChange}
            focusSpot={finfocusSpot}
            pinsHandler={finPinsHandler}
            ref={(el) => { finfacRef.current.push(el); }}
          />
          <Pins
            classList="fin-sub-pin"
            mainTab="tab1"
            data={sFinFacData}
            sFinFacData={sFinFacData}
            hillsFacData={sHillsFacData}
            pinState={finPinsState}
            addTransition={null}
            setImageChange={null}
            focusSpot={null}
            pinsHandler={null}
          />
          <Zoning />
          <div className={
            `masterplan-cabin
            ${cabinMapController ? 'show-masterplan-cabin' : ''}`
          }
          >
            {generalImg.images[0].masterplan_cabin.map((list, index) => (
              <img
                className={`
                  masterplan-cabin-img
                  masterplan-${list.cabinTitle}-img
                  ${cabinFocus === index + 1 ? 'focus-masterplan-cabin-img' : ''}
                  ${cabinFocus === 4 ? 'all-masterplan-cabin-img' : ''}
                  `}
                src={process.env.PUBLIC_URL + list.cabinImg}
                alt={list.cabinTitle}
              />
            ))}
          </div>
        </div>
        {
                /*
        <div
          className={`
            s-hills-pin-wrap
            ${pinChanger === 'hills-hover' ? 'show-s-hills-pin-wrap' : ''}
          `}
          ref={mapController === 'tab2' ? pinContainerRef : null}
        >
          <Pins
            data={sHillsData}
            sFinFacData={sFinFacData}
            hillsFacData={sHillsFacData}
            pinState={hillsPinsState}
            focusSpot={hillsfocusSpot}
            pinsHandler={hillsPinsHandler}
            addTransition={addTransition}
            setImageChange={setImageChange}
            mainTab="tab2"
            classList="s-hills-pin"
            ref={(el) => { hillsfacRef.current.push(el); }}
          />
        </div>

        <div
          className={pinChanger === 'hills-hover' ? 's-hills-fac-pin-wrap show-s-hills-fac-pin-wrap' : 's-hills-fac-pin-wrap'}
        >
          <Pins
            data={sHillsFacData}
            sFinFacData={sFinFacData}
            hillsFacData={sHillsFacData}
            pinState={hillsPinsState}
            addTransition={null}
            setImageChange={null}
            focusSpot={null}
            pinsHandler={null}
            mainTab="tab2"
            classList="s-hills-fac-pin"
            ref={(el) => { hillsfacRef.current.push(el); }}
          />
        </div>
                */
              }

        <div
          className={`
            layer
            layer-left
            ${mapController === 'tab1' ? 'show-layer-left' : ''}
            ${pinChanger === 'fin-cabin-mode' ? 'move-layer-left' : ''}
          `}
        />
        <div
          className={`layer layer-right ${mapController === 'tab2' ? 'show-layer-right' : ''}`}
        />
        <div className={`background-map ${pinChanger === 'fin-cabin-mode' ? 'move-background-map' : ''}`}>
          <img src={process.env.PUBLIC_URL + generalImg.images[0].masterplan_dk} alt="sono fin map" />
        </div>
      </div>
    </div>
  );
}

export default forwardRef(PinContainer);
