/* eslint-disable no-param-reassign */
import { createSlice } from '@reduxjs/toolkit';

export const detailPopupState = createSlice({
  name: 'detailPopupController',
  initialState: {
    value: false,
  },
  reducers: {
    setDetailPopupFalse: (state) => {
      state.value = false;
    },
    setDetailPopupTrue: (state) => {
      state.value = true;
    },
    setDetailPopupByState: (state, action) => {
      state.value = action.payload;
    },
  },
});

export const {
  setDetailPopupFalse,
  setDetailPopupTrue,
  setDetailPopupByState,
} = detailPopupState.actions;

export default detailPopupState.reducer;
